          let params = 'code=' + this.ruleForm.eqVarietyCode;
          if (this.deviceTypeList !== '' && this.ruleForm.eqVarietyCode) {
            setAppService.getDeviceType(params).then(res => {
              this.deviceTypeList = [];
              for (let i = 0, len = res.length; i < len; i++) {
